	module.exports = {"data":{"markdownRemark":{"html":"<h2>Five Levels of Agile Planning</h2>\n<p>A product owner needs to be familiar with the five levels of agile planning:</p>\n<ul>\n<li>Defining the product vision</li>\n<li>Defining the product roadmap</li>\n<li>Release planning</li>\n<li>Sprint planning</li>\n<li>Accounting for the outcome of daily scrums</li>\n</ul>\n<p>Agile planning is always continuous and should be revised at least every three months.</p>\n<h2>Brief Synopsis of the Five Levels of Agile Planning</h2>\n<ol>\n<li>\n<p>Product Vision: What (Summary of the major benefits &#x26; features the product will provide), Who (Stakeholders), Why (Need &#x26; Opportunity), When (Project scheduling &#x26; time expectations), Constraints and Assumptions (impacts risk &#x26; cost).</p>\n</li>\n<li>\n<p>Product Roadmap: Releases - Date, Theme/Feature Set, Objective, Development Approach.</p>\n</li>\n<li>\n<p>Release Planning: Iteration, Team Capacity, Stories, Priority, Size, Estimates, Definition of Done.</p>\n</li>\n<li>\n<p>Sprint Planning: Stories - Tasks, Definition of Done, Level-of Effort, Commitment</p>\n</li>\n<li>\n<p>Daily Planning: What did I do yesterday? What will I do today? What is blocking me?</p>\n</li>\n</ol>\n<h4>More Information:</h4>\n<p><a href='https://www.scrumalliance.org/why-scrum/agile-atlas/agile-atlas-common-practices/planning/january-2014/five-levels-of-agile-planning' target='_blank' rel='nofollow'>Five Levels of Agile Planning</a></p>","fields":{"slug":"/agile/five-levels-of-agile-planning/"},"frontmatter":{"title":"Five Levels of Agile Planning"}}},"pathContext":{"slug":"/agile/five-levels-of-agile-planning/"}}

